<template>
  <div>
    <h1>{{ msg }}</h1>
    <p>
      <el-icon><el-icon-s-promotion /></el-icon>
      vue version: {{ vueVersion }}, element-plus version: {{ elementVersion }}
    </p>
  </div>
</template>

<script>
import { Promotion as ElIconSPromotion } from '@element-plus/icons'
import ElementUI from 'element-plus'
import * as Vue from 'vue'

export default {
  components: {
    ElIconSPromotion,
  },
  name: 'HelloWorld',
props: {
