import './public-path'
import ElementUI from 'element-plus'
import 'element-plus/dist/index.css'
import * as Vue from 'vue'
import * as VueRouter from 'vue-router'
import App from './App.vue'
import routes from './router'
import store from './store'


let router = null
let instance = null

function render(props = {}) {
  const { container } = props
  router = VueRouter.createRouter({
    history: VueRouter.createWebHistory(
      window.__POWERED_BY_QIANKUN__ ? '/vue' : '/'
    ),
    routes,
  })

  instance = window.$vueApp = Vue.createApp(App)
  window.$vueApp.config.globalProperties.routerAppend = (
    path,
    pathToAppend
  ) => {
    return path + (path.endsWith('/') ? '' : '/') + pathToAppend
  }
  window.$vueApp.use(store)
  window.$vueApp.use(router)
  window.$vueApp.use(ElementUI)
  window.$vueApp.mount(container ? container.querySelector('#app'): '#app')
}

if (!window.__POWERED_BY_QIANKUN__) {
  render()
}

function storeTest(props) {
  props.onGlobalStateChange &&
    props.onGlobalStateChange(
      (value, prev) =>
        console.log(`[onGlobalStateChange - ${props.name}]:`, value, prev),
      true
    )
  props.setGlobalState &&
    props.setGlobalState({
      ignore: props.name,
      user: {
        name: props.name,
      },
    })
}

export async function bootstrap() {
  console.log('[vue] vue app bootstraped')
}

export async function mount(props) {
  console.log('[vue] props from main framework', props)
  storeTest(props)
  render(props)
}

export async function unmount() {
  instance.unmount();
  instance = null;
  router = null

}
